.App {
    text-align: center;
  }
  
  .App-logo {
    height: 20vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    /* .App-logo {
      animation: App-logo-spin infinite 20s linear;
    } */
  }
  
  .App-header {
    background-color: #000000;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    font-size: calc(8px + 2vmin);
    color: white;
    padding-top: 30%;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  .fade-in-text-slow {
    animation: fadeIn 3s ease-in-out; /* Adjust the animation duration and easing as needed */
  }
  
  .fade-in-text {
    animation: fadeIn 2s ease-in-out; /* Adjust the animation duration and easing as needed */
  }

  .fade-in-text-fast {
    animation: fadeIn 1s ease-in-out; /* Adjust the animation duration and easing as needed */
  }
  
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  