/* Navbar.css */
.nav-buttons {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #141414;
  color: white;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #201d1d;
}

button {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 16px;
  outline: none;
}

button:hover {
  text-decoration: underline;
}
